"use client";

import { useEffect, useMemo, useState } from "react";
import {
    useReactTable,
    getCoreRowModel,
    getFilteredRowModel,
    getPaginationRowModel,
    getSortedRowModel,
} from "@tanstack/react-table";
import { Pencil, Trash2, Search, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
    Card,
    CardHeader,
    CardTitle,
    CardToolbar,
    CardTable,
    CardFooter,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea, ScrollBar } from "@/components/ui/scroll-area";
import {
    Tooltip,
    TooltipContent,
    TooltipProvider,
    TooltipTrigger,
} from "@/components/ui/tooltip";
import { DataGrid } from "@/components/ui/data-grid";
import { DataGridTable } from "@/components/ui/data-grid-table";
import { DataGridPagination } from "@/components/ui/data-grid-pagination";
import { useStreamCategory } from "./stream-category-context";
import { getStreamCategories } from "@/app/api/categories/categories";

const StreamCategoryTable = () => {
    const {
        streamCategories,
        setStreamCategories,
        refreshFlag,
        setStreamCategoryToEdit,
    } = useStreamCategory();

    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [pagination, setPagination] = useState({ pageIndex: 0, pageSize: 10 });
    const [sorting, setSorting] = useState([]);
    const [searchQuery, setSearchQuery] = useState("");

    useEffect(() => {
        const fetchCategories = async () => {
            try {
                setLoading(true);
                const token = localStorage.getItem("token");
                const data = await getStreamCategories(token);
                setStreamCategories(data);
                setError(null);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };
        fetchCategories();
    }, [refreshFlag, setStreamCategories]);

    const filteredData = useMemo(() => {
        if (!searchQuery) return streamCategories;
        return streamCategories.filter((cat) =>
            cat.name?.toLowerCase().includes(searchQuery.toLowerCase())
        );
    }, [searchQuery, streamCategories]);

    const columns = useMemo(
        () => [
            {
                id: "name",
                accessorFn: (row) => row.name,
                header: () => <span className="font-bold">Name</span>,
                cell: ({ row }) => (
                    <span
                        className="font-bold truncate block max-w-[150px]"
                        title={row.original.name}
                    >
                        {row.original.name}
                    </span>
                ),
                size: 150,
            },
            {
                id: "createdAt",
                accessorFn: (row) => row.createdAt,
                header: () => <span className="font-bold">Created At</span>,
                cell: ({ row }) =>
                    new Date(row.original.createdAt).toLocaleDateString("en-GB", {
                        day: "2-digit",
                        month: "short",
                        year: "numeric",
                    }),
                size: 150,
            },
            {
                id: "actions",
                header: () => <span className="font-bold block text-right">Actions</span>,
                cell: ({ row }) => (
                    <div className="flex justify-end gap-3 items-center">
                        <TooltipProvider>
                            <Tooltip>
                                <TooltipTrigger asChild>
                                    <button
                                        onClick={() => setStreamCategoryToEdit(row.original)}
                                        className="text-primary duration-300 cursor-pointer hover:text-blue-800"
                                    >
                                        <Pencil size={18} />
                                    </button>
                                </TooltipTrigger>
                                <TooltipContent>Edit</TooltipContent>
                            </Tooltip>
                        </TooltipProvider>
                        <TooltipProvider>
                            <Tooltip>
                                <TooltipTrigger asChild>
                                    <button className="text-red-600 cursor-pointer hover:text-red-800">
                                        <Trash2 size={18} />
                                    </button>
                                </TooltipTrigger>
                                <TooltipContent>Delete</TooltipContent>
                            </Tooltip>
                        </TooltipProvider>
                    </div>
                ),
                size: 90,
            },
        ],
        [setStreamCategoryToEdit]
    );

    const table = useReactTable({
        columns,
        data: filteredData,
        pageCount: Math.ceil(filteredData?.length / pagination.pageSize),
        getRowId: (row) => String(row._id),
        state: { pagination, sorting },
        onPaginationChange: setPagination,
        onSortingChange: setSorting,
        getCoreRowModel: getCoreRowModel(),
        getFilteredRowModel: getFilteredRowModel(),
        getPaginationRowModel: getPaginationRowModel(),
        getSortedRowModel: getSortedRowModel(),
        columnResizeMode: "onChange",
    });

    if (loading)
        return (
            <div className="flex justify-center items-center min-h-[200px]">
                Loading categories...
            </div>
        );
    if (error)
        return <div className="p-4 bg-red-100 text-red-700 rounded">{error}</div>;

    return (
        <DataGrid table={table} recordCount={filteredData?.length || 0}>
            <Card>
                <CardHeader className="py-3.5">
                    <CardTitle>Stream Categories</CardTitle>
                    <CardToolbar className="relative">
                        <Search className="absolute start-2 text-primary duration-300 top-1/2 -translate-y-1/2" />
                        <Input
                            placeholder="Search categories..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="ps-9 max-w-[300px] w-full"
                        />
                        {searchQuery && (
                            <Button
                                variant="ghost"
                                className="absolute end-1.5 top-1/2 -translate-y-1/2 h-6 w-6"
                                onClick={() => setSearchQuery("")}
                            >
                                <X />
                            </Button>
                        )}
                    </CardToolbar>
                </CardHeader>
                <CardTable>
                    <ScrollArea>
                        <DataGridTable />
                        <ScrollBar orientation="horizontal" />
                    </ScrollArea>
                </CardTable>
                <CardFooter>
                    <DataGridPagination />
                </CardFooter>
            </Card>
        </DataGrid>
    );
};

export default StreamCategoryTable;
